import React, { useContext } from "react";
import { Box, Button, Typography } from "@mui/material";
import { Google } from "@mui/icons-material";
import { makeStyles } from "@mui/styles";
import styled from "@emotion/styled";
import { GoogleLogin } from "react-google-login";
// import { useDispatch } from "react-redux";
// import { loginStart } from "../../store/auth/actions.js";
import { useNavigate } from "react-router-dom";
import { AppContext } from "../../context";

export const Login = () => {
  const navigate = useNavigate();
  const { setUserInfo } = useContext(AppContext);
  // const dispatch = useDispatch();
  const responseGoogle = (response) => {
    // dispatch(loginStart(response));

    if (response?.accessToken) {
      localStorage.setItem("user", JSON.stringify(response));
      localStorage.setItem("jwt", JSON.stringify(response.accessToken));
      navigate("/");
      setUserInfo(response);
    }
  };
  const classes = useStyles();

  const TitleLogin = styled(Typography)`
    font-size: 32px;
    font-weight: 500;
    display: flex;
    justify-content: center;
    margin: 42px auto 16px auto;
    color: white;
    text-shadow: 1px 1px 1px rgba(255, 255, 255, 0.3);
  `;

  const ButtonLoginGoogle = styled(Button)`
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.1);
    display: inline-flex;
    width: fit-content;
    height: 52px;
    padding: 12px 32px;
    background-color: white;
    &:hover {
      background-color: white;
      box-shadow: 0 8px 24px rgba(0, 0, 0, 0.14);
    }
    & svg {
      margin-right: 12px;
    }
  `;
  return (
    <Box className={classes.root}>
      <Box className={classes.boxLogin}>
        <TitleLogin>Login for Project X</TitleLogin>
        <Box className={classes.boxLoginButton}>
          <GoogleLogin
            clientId="276034369117-h6cfooe2g8g92jm11flh0juk2livbgn7.apps.googleusercontent.com"
            render={(renderProps) => (
              <ButtonLoginGoogle
                onClick={renderProps.onClick}
                disabled={renderProps.disabled}
              >
                <Google />
                Login with Google
              </ButtonLoginGoogle>
            )}
            buttonText="Login with Google"
            onSuccess={responseGoogle}
            onFailure={responseGoogle}
            cookiePolicy={"single_host_origin"}
          />
          {/* <GoogleLogout
            clientId="276034369117-fchc2rt7lnkapsfnu6akb7e76sdk21tk.apps.googleusercontent.com"
            buttonText="Logout"
            onLogoutSuccess={responseGoogle}
          ></GoogleLogout> */}
        </Box>
      </Box>
    </Box>
  );
};
const useStyles = makeStyles({
  root: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    with: "100vw",
    height: "100vh",
    backgroundColor: "#654ea3",
    background: "linear-gradient(to right, #654ea3, #eaafc8)",
  },
  boxLogin: {
    width: "450px",
    height: "320px",
    boxShadow: "0 8px 16px rgba(0,0,0,.1)",
    borderRadius: "6px",
    backgroundColor: "#7f7fd5",
    background: "linear-gradient(to bottom, #7f7fd5, #86a8e7, #91eae4)",
  },
  boxLoginTitle: {
    fontSize: "32px",
    fontWeight: 500,
    display: "flex",
    justifyContent: "center",
    margin: "42px auto 16px auto",
    color: "white",
    textShadow: "1px 1px 1px rgba(255,255,255,0.3)",
  },
  boxLoginButton: {
    display: "flex",
    justifyContent: "center",
    marginTop: "80px",
  },
  buttonLogin: {
    boxShadow: "0 0 8px rgba(0,0,0,.1)",
    display: "inline-flex",
    width: "fit-content",
    height: "52px",
    padding: "12px 32px",
    backgroundColor: "white",
    "&:hover": {
      backgroundColor: "white",
      boxShadow: "0 8px 24px rgba(0,0,0,.14)",
    },
    "& svg": {
      marginRight: "12px",
    },
  },
});
